.filmList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.filmItem:hover {
  transform: scale(1.05);
}

.filmTitle {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 10px;
  font-size: 20px;
  font-weight: bold;
  color: black;
}
.filmItem {
  margin: 15px;
  width: 260px;
  flex-basis: calc(100% / 4 - 30px);
  border: 1px solid white;
  transition: 250ms;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
